.clientWrap {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  max-width: 350px;
  margin: 25px auto;
  text-align: center;
  background-color: #f5f9fc;
}

.clientHeader {
  padding: 5px;
}

.clientId {
  color: grey;
  font-size: 15px;
}

.clientButtons {
  display: inline-flex;
}

.toggleButton {
  min-width: 100px;
  min-height: 50px;
}

.buildingsHeader {
  font-family: sans-serif;
  padding: 10px;
  font-size: 15px;
  font-weight: bold;
}

.clientBody {
  padding-bottom: 5px;
}
