.bottom-login {
    background-image: url('./bg.png');
    width: calc(100% - 320px);
    padding: 50px 160px;
    display: flex;
    flex-wrap: wrap;
    /*height: '50vh'*/
}
@media screen and (max-width: 720px) {
    .bottom-login {
        box-sizing: border-box;
        width: 100%;
        padding: 50px 20px;
        margin: auto;
    }
}

