.table {
  border-collapse: collapse;
  margin: 25px auto;
  font-size: 0.9em;
  font-family: sans-serif;
  min-width: 300px;
}

.table caption {
  font-family: sans-serif;
  padding: 10px;
  font-size: 15px;
  font-weight: bold;
}

.tableCell {
  border: 1px solid grey;
  padding: 4px;
  text-align: left;
}

.tableHeader {
  background-color: #8298ab;
  color: #ffffff;
  text-align: left;
  border: 1px solid grey;
}

.tableWrap {
  padding: 0px;
}
